const axios = require("axios");

class model {
  state = {
    imageArray: [],
    smashTheCodeScore: 0,
    tonesArray: [],
  };

  //getters to return state
  get currentState() {
    return this.state;
  }

  //gets level1 images
  async getImages() {
    var tempArray;
    var that = this;
    var res = await axios.get("api/v1/images");
    tempArray = res.data.data.data;
    let randomArray = this.UniqueRandomNum(10, tempArray.length);

    randomArray.forEach(function(element, index, array) {
      //selecting 10 random images from the imageArray
      that.state.imageArray.push(tempArray[element - 1]);
    });
    return;
  }

  //gets level1 tones
  async getTones() {
    var Howmany = 10;

    var res = await axios.get(
      `http://127.0.0.1:5000/getTones/?tones = ${Howmany}` //http://tones7.97tech.tk/
    );

    this.state.tonesArray = res.data.data.tones;

    return;
  }

  UniqueRandomNum(limit, howMany) {
    let array = [];
    for (let i = 1; i <= limit; i++) {
      array.push(i);
    }

    let res = [];

    for (let i = 1; i <= howMany; i++) {
      const rand = Math.floor(Math.random() * (limit - i));
      res.push(array[rand]);
      array[rand] = array[limit - i];
    }

    return res;
  }

  async loginUser(email, password) {
    axios.defaults.withCredentials = true;
    try {
      const res = await axios({
        method: "POST",
        url: "http://127.0.0.1:3000/api/v1/users/login",
        data: {
          email,
          password,
        },
      });

      if (res.data.status === "success") {
        console.log(res);
      }
    } catch (err) {
      alert("error", err.response.data.message);
    }

    await axios.get("http://127.0.0.1:3000/api/v1/users/isLoggedin");
  }
}

module.exports.modelObj = new model();
